// В этом файле должны быть стили для БЭМ-блока clinic, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.lending {
  $block-name: &; // #{$block-name}__element

  h2 {
    @extend .h2;
    grid-column: 4 / 6 span;
    margin-bottom: 40px;
    @media (max-width: 1600px) {
      grid-column: 4 / 7 span;
    }
    @media (max-width: 1025px) {
      grid-column: 3 / 8 span;
      margin-bottom: 24px;
    }
    @media (max-width: 990px) {
      grid-column: 2 / 6 span;
      margin-bottom: 24px;
    }
    @media (max-width: 599px) {
      grid-column: 1 / 8 span;
    }
    span {
      color: $f-primary;
      margin-bottom: 32px;
    }
  }
  p {
    grid-column: 4 / 6 span;
    margin: 0;
    @media (max-width: 1600px) {
      grid-column: 4 / 6 span;
    }
    @media (max-width: 1025px) {
      grid-column: 3 / 8 span;
    }
    @media (max-width: 990px) {
      grid-column: 2 / 6 span;
    }
    @media (max-width: 559px) {
      grid-column: 1 / 8 span;
    }
  }
  ul {
    grid-column: 4 / 6 span;
    padding-left: 26px;
    margin: 0;
    margin-bottom: 45px;
    @media (max-width: 1600px) {
      margin-bottom: 16px;
    }
    @media (max-width: 1024px) {
      grid-column: 3 / 9 span;
    }
    @media (max-width: 991px) {
      grid-column: 2 / 5 span;
    }
    @media (max-width: 559px) {
      grid-column: 1 / 8 span;
    }
    li {
      @extend .p3;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
      &::before {
        border-radius: 100%;
      }
    }
  }
  .link {
    grid-column: 4 / 6 span;
    @extend .s2;
    margin-top: 40px;
    @media (max-width: 1025px) {
      grid-column: 3 / 8 span;
    }
    @media (max-width: 990px) {
      grid-column: 2 / 5 span;
    }
    @media (max-width: 559px) {
      grid-column: 1 / 8 span;
    }
    svg path {
      fill: $ui-primary;
    }
  }
  &__banner {
    position: sticky;
    top: 0;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;
    background-color: $ui-light;
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    height: 768px;
    @media (max-width: 1366px) {
      height: 640px;
    }
    @media (max-width: 991px) {
      grid-template-columns: repeat(8, 1fr);
      gap: 8px;
      height: 420px;
      background-position: 20% bottom;
    }
    @media (max-width: 480px) {
      height: 333px;
    }
    &.hidden {
      z-index: -1;
    }
    &::before {
      position: absolute;
      content: "";
      bottom: 0;
      left: 0;
      width: 100%;
      height: 560px;
      background: linear-gradient(
        180deg,
        rgba(0, 8, 49, 0) 0%,
        rgba(0, 8, 49, 0.6) 100%
      );
    }
    h1 {
      @extend .h1;
      color: $f-white;
      align-self: end;
      grid-column: 4 / 5 span;
      margin-bottom: 60px;
      z-index: 1;
      @media (max-width: 991px) {
        grid-column: 2 / 5 span;
      }
    }
  }
  &__inner {
    position: relative;
    background-color: $ui-light;
    padding: 80px 40px 0;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;
    z-index: 1;
    @media (max-width: 991px) {
      padding: 40px 24px;
      grid-template-columns: repeat(8, 1fr);
      gap: 8px;
    }
  }
  &-clinic__number {
    position: relative;
    grid-column: 3 / 8 span;
    @include font(430px, 554px, 400);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    overflow: hidden;
    background-image: url("../img/clinic.gif");
    background-size: cover;
    @media (max-width: 1600px) {
      grid-column: 2 / 10 span;
    }
    @media (max-width: 1366px) {
      @include font(376px, 485px, 400);
    }
    @media (max-width: 1024px) {
      @include font(270px, 348px, 400);
    }
    @media (max-width: 991px) {
      @include font(170px, 170px, 400);
      grid-column: 1 / 8 span !important;
      text-align: center;
    }
    @media (max-width: 600px) {
      text-align: left;
      @include font(96px, 96px, 400);
    }
    & + p.h1 {
      margin-top: -80px;
      margin-bottom: 40px;
      @media (max-width: 991px) {
        margin-top: 0;
      }
    }
  }
  &-clinic__mid-number {
    grid-column: 3 / 8 span;
    @extend .h1;
    display: flex;
    align-items: end;
    margin: 160px 0 80px;
    justify-content: center;
    @media (max-width: 991px) {
      margin: 60px 0 40px;
      grid-column: 2 / 6 span;
    }
    span {
      @include font(430px, 300px, 300);
      @media (max-width: 1024px) {
        @include font(376px, 485px, 300);
      }
      @media (max-width: 991px) {
        @include font(140px, 96px, 300);
      }
    }
    p {
      width: min-content;
    }
  }
  &-clinic__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column: 2 / 10 span;
    gap: 20px;
    margin-top: 200px;
    @media (max-width: 1024px) {
      grid-column: 1 / 12 span;
    }
    @media (max-width: 991px) {
      grid-column: 1 / 8 span;
    }
    img {
      filter: drop-shadow(0px 39.5254px 98.8136px #d8e3fd);
    }
    &-left,
    &-right {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      gap: 20px;
      position: relative;
      z-index: 1;
    }
    &-left {
      align-items: end;
    }
    &-right {
      // padding-top: 120px;
    }
  }
  &-clinic__images + p + .link {
    margin-bottom: 120px;
    @media (max-width: 991px) {
      margin-bottom: 80px;
    }
    @media (max-width: 600px) {
      margin-bottom: 40px;
    }
  }
  &-clinic__wrapper {
    grid-column: 2 / 10 span;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 70px;
    margin-top: 70px;
    margin-bottom: 60px;
    @media (max-width: 1366px) {
      gap: 60px;
    }
    @media (max-width: 1024px) {
      grid-column: 1 / 12 span;
      gap: 20px;
    }
    @media (max-width: 991px) {
      grid-column: 1 / 8 span;
      margin-top: 40px;
      margin-bottom: 40px;
    }
    @media (max-width: 600px) {
      grid-template-columns: 1fr;
    }
    #{$block-name} {
      &-clinic__item {
        @media (max-width: 600px) {
          display: grid;
          grid-template-columns: repeat(8, 1fr);
          gap: 8px;
          gap: 0 20px;
        }
        &:nth-child(2).observer.animation {
          animation-delay: 0.1s;
        }
        &:nth-child(3).observer.animation {
          animation-delay: 0.2s;
        }
      }
      &-clinic__icon {
        margin-bottom: 8px;
        @media (max-width: 600px) {
          grid-column: 2 span;
          grid-row: 2 span;
        }
      }
      &-clinic__title {
        @extend .h3;
        color: $f-primary;
        @media (max-width: 600px) {
          grid-column: 3 / 6 span;
        }
      }
      &-clinic__desc {
        @extend .s3;
        @media (max-width: 600px) {
          grid-column: 3 / 6 span;
        }
      }
    }
  }
  &-clinic__licence {
    grid-column: 12 span;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;
    margin-top: 200px;
    margin-bottom: 160px;
    overflow: hidden;
    @media (max-width: 991px) {
      position: relative;
      grid-template-columns: repeat(8, 1fr);
      gap: 8px;
      grid-column: 1 / 8 span;
      margin-top: 80px;
      margin-bottom: 80px;
    }
    @media (max-width: 600px) {
      margin-top: 40px;
      margin-bottom: 40px;
    }
    .link {
      width: 100%;
      margin-top: 24px;
    }
    &-content {
      grid-column: 7 span;
      @media (max-width: 991px) {
        grid-column: 5 span;
      }
      @media (max-width: 600px) {
        grid-column: 8 span;
      }
    }
    &-img {
      grid-column: 8 / 5 span;
      @media (max-width: 991px) {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 265px;
        height: 265px;
        z-index: -1;
      }
      @media (max-width: 600px) {
        width: 240px;
        height: 240px;
        top: 30px;
        right: -40px;
        transform: none;
      }
      img {
        width: 100%;
      }
    }
    &-title {
      @extend .h1;
      margin-bottom: 24px;
      @media (max-width: 600px) {
        margin-bottom: 160px;
      }
    }
    &-desc {
      @extend .p2;
    }
  }
  &__template {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column: 12 span;
    grid-gap: 20px;
    @media (max-width: 991px) {
      grid-template-columns: repeat(8, 1fr);
      gap: 8px;
      grid-column: 1 / 8 span;
    }

    background: -webkit-gradient(
        linear,
        left top,
        right top,
        from(rgba(242, 246, 255, 0.95)),
        to(rgba(242, 246, 255, 0.95))
      )
      no-repeat;
    &-title {
      position: sticky;
      top: calc(126px + 32px);
      grid-column: 6 span;
      @media (max-width: 1366px) {
        grid-column: 7 span;
      }
    }
    &-bottom {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      grid-column: 12 span;
      grid-gap: 20px;
      position: relative;
      background: inherit;
      // background-position: 0 80px;
      margin-top: 80px;
      @media (max-width: 1024px) {
        margin-top: 40px;
      }
      @media (max-width: 991px) {
        grid-column: 8 span;
        grid-template-columns: repeat(8, 1fr);
        gap: 8px;
        margin-top: 0;
      }
      &::before {
        position: absolute;
        content: "";
        top: -80px;
        left: 0;
        width: 100%;
        height: 80px;
        transform: rotate(-180deg);
        background: linear-gradient(
          180deg,
          #f2f6ff 0%,
          rgba(242, 246, 255, 0.9) 52.87%,
          rgba(242, 246, 255, 0) 95%
        );
        background-repeat: no-repeat;
        background-size: cover;
        z-index: 1;
        @media (max-width: 1024px) {
          content: none;
        }
      }
    }
    &-desc {
      @extend .p2;
      grid-column: 4 span;
      height: max-content;
      @media (max-width: 1024px) {
        grid-column: 12 span;
      }
      @media (max-width: 991px) {
        grid-column: 1 / 8 span;
      }
    }
    &-aside {
      grid-column: 4 span;
      grid-row: 2;
      position: sticky;
      bottom: 70px;
      -ms-grid-row-align: end;
      align-self: end;
      margin-bottom: 70px;
      z-index: 999;
      @media (max-width: 1024px) {
        position: static;
        margin-bottom: 24px;
      }
      @media (max-width: 991px) {
        grid-column: 8 span;
      }
      .link {
        @extend .s1;
        @media (max-width: 1024px) {
          margin-top: 16px;
        }
      }
    }
    &-content {
      background-color: $ui-white;
      grid-column: 5 / 8 span;
      z-index: 1;
      box-shadow: 0 32px 80px #d8e3fd;
      margin-bottom: 160px;
      @media (max-width: 1024px) {
        grid-column: 1 / 12 span;
      }
      @media (max-width: 991px) {
        grid-column: 1 / 8 span;
        margin-bottom: 16px;
      }
    }
    .detail__inner {
      p {
        margin: 18px 0;
      }
    }
    .detail__link-all {
      .link {
        margin-top: 0;
      }
    }
  }
  .observer {
    opacity: 0;
    &.animation {
      animation: fadeInUp 1s forwards;
      animation-delay: 0s;
      animation-timing-function: $transitionTime;
    }
  }
  &-infographics {
    grid-column: 12 span;
    margin-bottom: 80px;
    @media (max-width: 991px) {
      grid-column: 8 span;
    }
    .green {
      color: $f-primary;
      .progress {
        background-color: $f-primary;
      }
      .line {
        span {
          background-color: $f-primary;
        }
      }
    }
    .grey {
      color: #8e8e8e;
      .progress {
        background-color: #8e8e8e;
      }
      .line {
        span {
          background-color: #8e8e8e;
        }
      }
    }
    &__wrapper {
      overflow-x: auto;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    &__inner {
      @media (max-width: 600px) {
        width: 700px;
      }
    }
    &__title {
      @extend .h1;
      grid-column: 2 / 8 span;
      margin-top: 120px;
      margin-bottom: 80px;
      @media (max-width: 1600px) {
        grid-column: 2 / 10 span;
      }
      @media (max-width: 990px) {
        grid-column: 2 / 6 span;
        margin-top: 80px;
        margin-bottom: 40px;
      }

      span {
        color: $f-primary;
      }
    }
    &__inner {
    }
    &__popup {
      &.active {
        @media (max-width: 991px) {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: $ui-white;
          z-index: 9999;
        }
      }

      span {
        position: absolute;
        content: "";
        z-index: 10;
        @media (min-width: 991px) {
          bottom: 100%;
          left: 50%;

          transform: translateX(-50%);
          width: 0;
          height: 0;
          border-left: 12px solid transparent;
          border-right: 12px solid transparent;
          border-bottom: 16px solid $ui-light2;
        }
        @media (max-width: 991px) {
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='18.3633' y='4.22168' width='2' height='20' transform='rotate(45 18.3633 4.22168)' fill='%230F1F3F'/%3E%3Crect x='19.7773' y='18.3638' width='2' height='20' transform='rotate(135 19.7773 18.3638)' fill='%230F1F3F'/%3E%3C/svg%3E%0A");
          right: 16px;
          top: 16px;
          width: 24px;
          height: 24px;
        }
      }
      &-item {
        position: relative;
        filter: drop-shadow(0px 32px 80px #d8e3fd);
        background-color: $ui-light2;
        padding: 24px;
        display: none;
        width: 650px;
        position: absolute;
        z-index: 10;
        @media (max-width: 991px) {
          width: 100%;
          height: 100%;
          overflow-y: auto;
        }
      }
      &-title {
        @extend .h6;
        color: $f-primary;
        margin-bottom: 8px;
      }
      &-desc {
        @extend .p4;
        p {
          margin-bottom: 16px;
        }
      }
    }
    &__stages {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      overflow: hidden;
      margin: 16px 0;
      @extend .s4;
      span {
        position: relative;
        cursor: pointer;
        border-bottom: 1px dashed $ui-dark;
        &.active {
          color: $f-primary;
          border: none;
        }
        @media (max-width: 991px) {
          max-width: 150px;
          text-align: center;
        }
        @media (max-width: 600px) {
          &:first-child {
            max-width: 55px;
            line-break: anywhere;
            margin-right: 45px;
          }
        }
      }
      span:first-child,
      span:last-child {
        flex-grow: 0;
      }
    }
    &__item {
      &-bar {
        position: relative;
        width: 100%;
        height: 16px;
        display: flex;
        justify-content: space-between;
        overflow: hidden;
        .line {
          position: relative;
          top: 50%;
          transform: translateY(-50%);
          display: inline-block;
          height: 4px;
          background-color: $ui-light;
          z-index: 1;
          width: calc(16px * 3);
          @media (max-width: 991px) {
            width: 20px;
          }
          &:first-of-type {
            margin-left: -16px;
            @media (max-width: 991px) {
              margin-left: -4px;
            }
          }
          &:last-of-type {
            margin-right: -16px;
            @media (max-width: 991px) {
              margin-right: -4px;
            }
          }
          &:not(:first-child) {
            &:after {
              position: absolute;
              content: "";
              top: 50%;
              transform: translateY(-50%);
              left: 0;
              width: 16px;
              height: 2px;
              background-color: $ui-borderlight;
              z-index: 1;
            }
          }
          &:not(:last-child) {
            &:before {
              position: absolute;
              content: "";
              top: 50%;
              transform: translateY(-50%);
              right: 0;
              width: 16px;
              height: 2px;
              background-color: $ui-borderlight;
              z-index: 1;
            }
          }

          span {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 16px;
            height: 16px;
            background-color: #8e8e8e;
            border-radius: 50%;
            z-index: 10;
            @media (max-width: 991px) {
              width: 12px;
              height: 12px;
            }
          }
        }
        &:before {
          width: 100%;
          height: 2px;
          position: absolute;
          content: "";
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          background-color: $ui-borderlight;
        }
        .progress {
          height: 4px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
        }
      }
      &-title {
        @extend .s3;
        font-weight: 600 !important;
      }
    }
  }
  &-painkiller {
    grid-column: 12 span;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 100% auto;
    margin: 0 -40px;
    padding: 120px 40px 150px;
    @media (max-width: 991px) {
      background-position: 30% bottom;
      background-size: 800px;
      padding: 80px 24px 45px;
      margin: 0 -24px;
      grid-column: 8 span;
      grid-template-columns: repeat(8, 1fr);
      gap: 8px;
    }
    @media (max-width: 600px) {
      padding: 40px 24px 90px;
    }
    &__title {
      @include font(98px, 100px, 300);
      grid-column: 2 / 7 span;
      @media (max-width: 1600px) {
        @include font(80px, 100px, 300);
        grid-column: 2 / 8 span;
      }
      @media (max-width: 1366px) {
        @include font(64px, 72px, 300);
      }
      @media (max-width: 990px) {
        @include font(32px, 36px, 300);
        grid-column: 1 / 6 span;
      }
      @media (max-width: 599px) {
        @include font(32px, 36px, 300);
        grid-column: 1 / 8 span;
      }
      span {
        color: $f-primary;
      }
    }
    &__desc {
      @extend .p2;
      grid-column: 2 / 5 span;
      @media (max-width: 990px) {
        grid-column: 1 / 5 span;
      }
      @media (max-width: 599px) {
        grid-column: 1 / 8 span;
      }
    }
  }
  &-gallery {
    grid-column: 12 span;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;
    padding: 120px 0;
    overflow: hidden;
    margin-right: -40px;
    @media (max-width: 991px) {
      grid-column: 8 span;
      grid-template-columns: repeat(8, 1fr);
      gap: 8px;
      margin-right: -24px;
      padding: 40px 0;
    }
    &__title {
      @extend .h1;
      grid-column: 2 / 5 span;
      @media (max-width: 991px) {
        grid-column: 1 / 8 span;
      }
    }
    &__wrapper {
      grid-column: 2 / 11 span;
      margin: 40px 0;
      overflow: hidden;
      @media (max-width: 991px) {
        grid-column: 1 / 8 span;
      }
    }
    &__item {
      counter-increment: photo;
      position: relative;
      cursor: pointer;
      &::before {
        @extend .h6;
        padding: 16px 24px;
        content: "Фото " counter(photo);
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: $ui-white;
        @media (max-width: 991px) {
          padding: 8px 14px;
        }
        @media (max-width: 600px) {
          padding: 8px 4px;
        }
      }
    }
    &__desc {
      @extend .p2;
      grid-column: 2 / 8 span;
      @media (max-width: 991px) {
        grid-column: 1 / 8 span;
      }
    }
  }
  &__equipment {
    grid-column: 2/10 span;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 60px;
    margin-bottom: 200px;
    gap: 120px;
    @media (max-width: 1366px) {
      gap: 100px;
      margin-top: 60px;
      margin-bottom: 120px;
    }
    @media (max-width: 1024px) {
      gap: 75px;
    }
    @media (max-width: 991px) {
      gap: 55px;
      margin-top: 40px;
      margin-bottom: 60px;
      grid-column: 1/8 span;
    }
    @media (max-width: 480px) {
      grid-template-columns: repeat(1, 1fr);
      margin: 40px 0;
      gap: 40px;
    }
    &-item {
      @media (max-width: 480px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;
      }
      &:nth-child(2).observer.animation {
        animation-delay: 0.1s;
      }
      &:nth-child(3).observer.animation {
        animation-delay: 0.2s;
      }
    }
    &-desc {
      margin-top: 10px;
      @extend .s3;
    }
  }
}
.see-also {
  height: calc(100vh - 126px);
  position: sticky;
  background: $ui-dark;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 20px;
  padding: 80px 0;
  bottom: 0;
  z-index: 0;
  @media (max-width: 991px) {
    grid-template-columns: repeat(8, 1fr);
    gap: 8px;
    padding: 24px 24px 0;
    height: auto;
  }
  a {
    @extend .s1;
    grid-column: 4 / 5 span;
    @media (max-width: 991px) {
      grid-column: 1 / 8 span;
    }
  }
  &__title {
    @extend .h1;
    color: $f-white;
    grid-column: 4 / 5 span;
    @media (max-width: 991px) {
      grid-column: 1 / 4 span;
    }
  }
}
