// В этом файле должны быть стили для БЭМ-блока pagination, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.pagination {
  position: relative;
  background: #f5f8ff;
  padding: 50px 0;
  display: flex;
  &:before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(50%, -50%);
    height: 56px;
    width: 1px;
    background-color: rgba(168, 195, 234, 0.5);
  }
  $block-name: &; // #{$block-name}__element

  &__block {
    flex-basis: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 32px;
  }
  &__show-more {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
  }
  &__list {
    display: flex;
    gap: 32px;
    padding: 0;
    margin: 0;
    li {
      cursor: pointer;
      transition: .3s;
      &:hover {
        color: $f-primary;
      }
      &:before {
        content: none;
      }
    }
  }
  &__left {
    transform: rotate(90deg);cursor: pointer;
  }
  &__right {
    transform: rotate(-90deg);cursor: pointer;
  }
}
