// В этом файле должны быть стили для БЭМ-блока search, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.search {
  $block-name: &; // #{$block-name}__element

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @include blur(20px);
  z-index: 99;
  display: none;

  &--show {
    animation: show 1s both;
  }
  &--hide {
    animation: hide 1s both;
  }
  &__wrapper {
    @extend .container;
    margin: 85px auto 0;
    width: 100%;
    @media (max-width: 991px) {
      margin: 24px auto 0;
    }
  }
  &__inner {
    grid-column: 3 / 8 span;
    @media (max-width: 991px) {
      grid-column: 1 / 8 span;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }
  &__title {
    @extend .h3;
    color: $f-white;
  }
  &__close {
    cursor: pointer;
  }
  &__input {
    position: relative;
    button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 40px;
      height: 24px;
    }
    input {
      width: 100%;
      background-color: $ui-input;
      border: none;
      padding: 24px 40px 24px 88px;
      @extend .s3;
      color: $f-white;
      &::placeholder {
        color: $f-white;
      }
      @media (max-width: 600px) {
        padding: 16px 20px;
      }
    }
  }
  &__result {
    .tabs {
      &__caption {
        @extend .h4;
        gap: 40px;
        margin: 56px 0 32px;

        @media (max-width: 991px) {
          margin: 24px 0;
        }
        li {
          &.active::before {
            content: none;
          }
        }
      }
      &__content {
        background: #f5f8ff;
        &-block {
          padding: 0 40px;
          overflow-y: auto;
          max-height: 64vh;
          .pagination {
            margin: 0 -40px;
          }
        }
        .link {
          border-bottom: 1px solid rgba(168, 195, 234, 0.5);
          &-text {
            @extend .s2;
            color: $f-black;
          }
        }
        .pagination {
          @extend .p2;
          color: $f-black;
          &__list {
            li {
              color: $f-black !important;
            }
          }
        }
        svg {
          path {
            fill: $f-black;
          }
        }
      }
    }
  }
}
