// В этом файле должны быть стили для БЭМ-блока review, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.review {
  $block-name: &; // #{$block-name}__element
  overflow: hidden;
  &__date {
    @extend .s4;
    margin-bottom: 16px;
    @media (max-width: 991px) {
      margin-bottom: 8px;
    }
  }
  &__title {
    @extend .h2;
    margin-bottom: 32px;
    @media (max-width: 991px) {
      margin-bottom: 12px;
    }
  }
  &__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 64px;
    margin-bottom: 32px;
    @media (max-width: 991px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 12px;
      margin-bottom: 12px;
    }
    &-item {
    }
    &-prop {
      @extend .s4;
      margin-bottom: 4px;
    }
    &-value {
      @extend .s2;
      color: $f-primary;
    }
  }
  &__desc {
    @extend .p3;
    margin: 32px 0;
    @media (max-width: 991px) {
      margin: 12px 0;
    }
  }
  &__images {
    position: relative;
    width: 320px;
    height: 150px;
    overflow-y: hidden;
    overflow-x: auto;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      object-fit: cover;
    }
  }
}
