// В этом файле должны быть стили для БЭМ-блока mainPage, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority



.main-page {
  $block-name: &; // #{$block-name}__element
  background: #0b133d;
  @media (min-width: 992px) {
    height: 100vh;
    overflow: hidden;
  }
  &__title {
    @extend .h1;
    color: $f-white !important;
    margin-bottom: 32px;
    @media (max-width: 1366px) {
      margin-bottom: 24px;
    }
    &.fadeInUpHide {
      animation: fadeInUpHide 1s both 0.4s !important;
    }
  }
  &__link {
    &.fadeInUpHide {
      animation: fadeInUpHide 1s both 0.5s !important;
    }
  }
  &__wrapper {
    width: 100%;
    @media (min-width: 1921px) {
      .container {
        position: relative;
      }
    }
    @media (min-width: 992px) {
      background-image: none !important;
    }
    @media (max-width: 991px) {
      position: relative;
      & > .container {
        gap: 0;
        padding: 0;
      }
    }
    &:first-child {
      #{$block-name}__left {
        .link {
          @extend .s1;
          display: block;
        }
      }
    }
    &:last-child {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @include blur(20px);
    }

    &.swiper-slide-active {
      #{$block-name}__title {
        animation: fadeInUp 1s both;
        animation-delay: 1s;
        animation-timing-function: $transitionTime;
      }
      #{$block-name}__link {
        animation: fadeInUp 1s both 1.1s;
        animation-timing-function: $transitionTime;
      }
      #{$block-name}__right-item {
        animation: fadeInUp 1s both;
        animation-delay: 1s;
        animation-timing-function: $transitionTime;
      }
    }
  }
  &__inner {
    @extend .container;
    grid-template-rows: 1fr auto;
  }
  &__left {
    grid-column: 7 span;
    align-self: end;
    margin-bottom: 56px;
    @media (max-width: 991px) {
      position: sticky;
      top: 0;
      grid-column: 8 span;
      height: 600px;
      background-size: 100% 100%;
      background-position: center;
      background-attachment: fixed;
      background-repeat: no-repeat;
      margin-bottom: 0;
      padding: 0 24px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      padding-bottom: 33px;
    }
    @media (max-width: 480px) {
      height: 400px;
      padding: 16px;
    }
    .link {
      max-width: 550px;
      align-items: center;
      @extend .s2;
      svg path {
        fill: $f-white;
      }
      &-text {
        color: $f-white !important;
      }
      &:hover {
        svg path {
          fill: $f-primary;
        }
        .link-text {
          color: $f-primary !important;
        }
      }
    }
  }
  &__right {
    position: absolute;
    right: 0;
    width: 100%;
    max-width: 650px;
    grid-column: -4 / 4 span;
    align-self: end;
    @include blur(10px);
    display: grid;
    grid-template-columns: 1fr;
    animation: fadeIn 1s both 0.5s $transitionTime;
    @media (max-width: 1600px) {
      max-width: 550px;
    }
    @media (max-width: 1366px) {
      max-width: 400px !important;
    }
    @media (max-width: 1024px) {
      max-width: 360px !important;
    }
    @media (max-width: 991px) {
      max-width: none !important;
      position: static;
      grid-column: 1 / 8 span;
    }
    &.fadeInHide {
      animation: fadeInHide 0.5s both !important;
    }
    &--full {
      @media (min-width: 992px) {
        height: 100%;
        padding-top: 114px;
      }
      .main-page__right-item:not(:last-child) {
        position: relative;
        &::before {
          position: absolute;
          content: "";
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
          width: calc(100% - 80px * 2);
          height: 1px;
          background: #8ca5cb99;
          @media (max-width: 1366px) {
            width: calc(100% - 40px * 2);
          }
          @media (max-width: 991px) {
            width: 100%;
          }
        }
        .link {
          &:hover {
            .link-text {
              color: $f-primary;
            }
            svg {
              path {
                fill: $f-primary;
              }
            }
          }
          .link-text {
            color: $f-white;
          }
          svg {
            path {
              fill: $f-white;
            }
          }
        }
      }
    }
    &--row {
      @media (max-width: 991px) and (min-width: 481px) {
        display: flex;
        padding: 45px 24px;
        width: auto;
        #{$block-name}__right-item {
          align-items: start;
          padding: 0;
          gap: 0;
        }
        #{$block-name}__right-title {
          flex-grow: 1;
        }
        & > *::before {
          content: none !important;
        }
      }
    }
    &-item {
      padding: 0 80px;

      &:only-child {
        padding: 62px 80px;
      }
      img {
        width: 56px;
        height: 56px;
      }
      @media (min-width: 992px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
      @media (max-width: 1366px) {
        padding: 0 40px;
        &:only-child {
          padding: 40px;
        }
      }
      @media (max-width: 991px) {
        padding: 16px;

        &:only-child {
          padding: 16px;
        }
      }
      @media (max-width: 991px) and (min-width: 481px) {
        &:not(a) {
          display: grid;
          grid-template-columns: auto 1fr;
          grid-template-rows: 1fr 1fr;
          align-items: center;
          gap: 0 24px;
        }
        &:not(a) > .main-page__right-number:nth-child(1) {
          grid-column: 1;
          grid-row: 3 span;
          align-self: center;
          justify-self: center;
        }
        &:not(a) > .main-page__right-title:nth-child(2) {
          grid-column: 2;
          grid-row: 2 span;
        }
        &:not(a) > *:nth-child(3) {
          grid-column: 2;
        }
      }
      @media (max-width: 480px) {
        &:not(a):not(:first-child) {
          display: flex;
          align-items: center;
          gap: 24px;
        }
      }
      &--inner {
        display: grid;
        grid-template-columns: auto 1fr;
        grid-template-rows: auto 1fr;
        grid-gap: 0 32px;
        grid-row: 2 span;
        @media (max-width: 991px) and (min-width: 481px) {
          height: 100%;
        }
        @media (max-width: 991px) {
          display: flex;
          flex-direction: column;
        }
        #{$block-name} {
          &__right {
            &-title {
              grid-column: 2;
            }
            &-text {
              grid-column: 2;
            }
          }
        }
      }
      .link {
        width: 100%;
        justify-content: space-between;
        &--refresh {
          margin-top: 16px;
          @media (max-width: 991px) {
            margin-top: 8px;
          }
          .link-text {
            color: $f-primary;
          }
          svg path {
            fill: $f-primary;
          }
          &:hover {
            .link-text {
              color: $f-hover;
            }
            svg path {
              fill: $f-hover;
            }
          }
        }
        &-text {
          @extend .p2;
        }
        svg path {
          fill: $f-primary;
        }
      }
    }
    &-number {
      @extend .h1;
      color: $f-white;
      display: inline-flex;
      align-items: baseline;
      span {
        @extend .h2;
        color: $f-white;
      }
      img {
        margin-bottom: 8px;
      }
      @media (max-width: 991px) {
        @include font(60px, 60px, 400);
      }
    }
    &-title {
      @extend .s2;
      color: $f-white !important;
    }
    &-text {
      @extend .p4;
      color: $f-white !important;
    }
  }
}
