// В этом файле должны быть стили для БЭМ-блока detail, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.detail {
  $block-name: &; // #{$block-name}__element
  &--dark {
    background: linear-gradient(
        left,
        rgba(11, 19, 61, 0.95),
        rgba(11, 19, 61, 0.95)
      )
      no-repeat;
    @media (max-width: 991px) {
      background-color: rgba(11, 19, 61, 0.95);
    }
    #{$block-name} {
      &__top {
        padding-top: 120px;
        @media (max-width: 1366px) {
          padding-top: 160px;
        }
        @media (max-width: 991px) {
          padding-top: 60px;
          padding-bottom: 24px;
        }
        @media (max-width: 600px) {
          padding-top: 0;
          padding-bottom: 24px;
        }
      }
      &__bottom {
        &::before {
          position: absolute;
          content: "";
          top: 0;
          left: 0;
          width: 100%;
          height: 150px;
          background: linear-gradient(
            180deg,
            rgba(11, 19, 61, 0) 0%,
            rgba(11, 19, 61, 0.6) 39.06%,
            rgba(11, 19, 61, 0.95) 100%
          );
          z-index: 1;
          @media (max-width: 991px) {
            content: none;
          }
        }
      }
    }
  }
  &--light {
    background: linear-gradient(
        left,
        rgba(242, 246, 255, 0.95),
        rgba(242, 246, 255, 0.95)
      )
      no-repeat;
    @media (max-width: 991px) {
      background-color: rgba(242, 246, 255, 0.95);
    }
    #{$block-name} {
      &__top {
        padding-top: 120px;
        @media (max-width: 1600px) {
          padding-top: 150px;
        }
        @media (max-width: 991px) {
          padding-top: 0px !important;
          padding-bottom: 32px;
        }
      }
      &__bottom {
        &::before {
          position: absolute;
          content: "";
          top: 0;
          left: 0;
          width: 100%;
          height: 150px;
          transform: rotate(-180deg);
          background: linear-gradient(
            180deg,
            #f2f6ff 0%,
            rgba(242, 246, 255, 0.9) 52.87%,
            rgba(242, 246, 255, 0) 95%
          );
          background-repeat: no-repeat;
          background-size: cover;
          z-index: 1;
        }
      }
      &__content {
        box-shadow: 0px 32px 80px #d8e3fd;
      }
    }
  }
  @media (max-width: 991px) {
    padding-top: 68px;
  }
  @media (max-width: 500px) {
    padding-top: 58px;
  }
  &__aside {
    position: sticky;
    bottom: 70px;
    align-self: end;
    margin-bottom: 70px;
    z-index: 999;
    grid-column: 1 / 2 span;
    &.column3 {
      grid-column: 1 / 3 span;
    }
    
    &--show {
      span {
        @extend .s2;
        color: $f-white;
      }
      button {
        position: relative;
        color: $f-primary;
        @extend .s1;
        cursor: pointer;
        &:before {
          position: absolute;
          content: "";
          bottom: 0;
          left: 0;
          background-image: url("data:image/svg+xml,%3Csvg width='190' height='2' viewBox='0 0 190 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline y1='1' x2='190' y2='1' stroke='%2349AC32' stroke-width='2' stroke-dasharray='2 8'/%3E%3C/svg%3E%0A");
          background-size: cover;
          width: 100%;
          height: 2px;
        }
      }
    }
    @media (max-width: 1024px) {
      display: none;
    }

    .link {
      @extend .s1;
      display: inline;
      svg {
        margin-left: 12px;
        path {
          fill: $f-primary;
        }
      }
    }
  }
  &__show {
  }
  .tabs {
    &__caption {
      background: $ui-light;
      padding: 0 64px;
      gap: 56px;
      border-bottom: 1px solid $ui-borderlight;
      @media (max-width: 991px) {
        padding: 0 16px;
        gap: 20px;
        overflow-x: auto;
      }
      li {
        @extend .s2;
        padding: 40px 0;
        transition: 0.3s;
        @media (max-width: 991px) {
          padding: 20px 0;
        }
        &.active {
          &::before {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 4px;
            background-color: $f-primary;
          }
        }
      }
    }
  }
  &__bg {
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: -1;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    @media (max-width: 991px) {
      display: none;
    }
    & > * {
      object-fit: cover;
    }
  }
  &__inner {
    padding: 40px;
    @media (max-width: 991px) {
      padding: 24px 16px;
    }
    figure {
      margin: 0 0 24px 0;
      img {
        width: 100%;
      }
      figcaption {
        margin-top: 10px;
        @extend .p3;
      }
    }
    h2 {
      @extend .h2;
    }
    p {
      @extend .p3;
    }
  }
  &__top {
    position: sticky;
    top: 0;
    width: 100%;
    padding-bottom: 80px;

    &-inner {
      @extend .container;
      position: relative;
      // padding-top: 315px;
    }
    @media (min-width: 992px) {
      background-image: none !important;
    }

    @media (max-width: 991px) {
      background-size: cover;
      // background-position: center;
      background-repeat: no-repeat;
    }

    .breadcrumbs {
      grid-column: 12 span;
      @media (max-width: 991px) {
        grid-column: 8 span;
      }
    }
    .link {
      margin-top: 15px;
      margin-bottom: 0;
      grid-column: 12 span;
      @extend .s3;
      @media (max-width: 991px) {
        grid-column: 8 span;
      }
      @media (max-width: 480px) {
        margin-top: 40px;
      }
      svg path {
        fill: $ui-primary;
      }
    }

    .detail-service {
      &__title {
        margin-top: 12px;
        grid-column: 7 span;
        @media (max-width: 1600px) {
          grid-column: 8 span;
        }
        @media (max-width: 1024px) {
          grid-column: 10 span;
        }
        @media (max-width: 991px) {
          margin-top: 24px;
          margin-bottom: 12px;
        }
      }
      &__desc {
        @extend .p2;
        grid-column: 6 span;
      }
    }
    .detail-surgeon {
      &__img {
        position: absolute;
        right: 200px;
        top: -35px;
        width: 35%;
        @media (max-width: 1366px) {
          right: 100px;
          top: 0;
          width: 40%;
        }
        @media (max-width: 991px) {
          right: 0;
          width: 40%;
        }
        @media (max-width: 767px) {
          bottom: -32px;
          width: 40%;
        }
        @media (max-width: 601px) {
          bottom: -60px;
          width: auto;
          max-width: 320px;
        }
        @media (max-width: 481px) {
          bottom: 0px;
          top: 25px;

          max-width: 240px;
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          @media (max-width: 480px) {
            height: 100%;
          }
        }
      }
      &__title {
        margin-top: 12px;
        grid-column: 5 span;

        @media (max-width: 1600px) {
          grid-column: 6 span;
        }
        @media (max-width: 1024px) {
          grid-column: 6 span;
        }
        @media (max-width: 991px) {
          grid-column: 5 span !important;
          margin-top: 24px;
          margin-bottom: 12px;
        }
        @media (max-width: 480px) {
          margin-top: 120px;
          h1 {
            position: relative;
            z-index: 1;
          }
          &::before {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: calc(100% + 32px);
            background-color: $ui-white;
            z-index: -1;
          }
        }
      }
      &__desc {
        @extend .p2;
        grid-column: 6 span;
        grid-row: 3;
        @media (max-width: 991px) {
          grid-column: 4 span;
        }
        @media (max-width: 480px) {
          grid-column: 8 span;
          z-index: 1;
          background-color: $ui-light;
          margin: 0 -16px -32px;
          padding: 0 16px 20px;
        }
        span {
          @extend .s3;
        }
      }
    }
  }
  &__title {
    grid-column: 1 / 9 span;
    margin-top: 210px;
    color: $f-white;
    max-width: 620px;
    z-index: 1;
  }
  &__bottom {
    position: relative;
    background: inherit;
    background-position: 0 150px;
    &-inner {
      @extend .container;
    }
  }
  &__content {
    margin-bottom: 40px;
    grid-column: 4 / 9 span;
    background: $ui-white;
    z-index: 2;
    @media (max-width: 1024px) {
      grid-column: 12 span;
      margin: 0 -16px;
    }
    &-inner {
      padding: 64px;
    }
    &-links {
      display: grid;
      .link,
      .link-desc {
        padding: 40px 64px;
        color: $f-black;
        border-bottom: 1px solid $ui-borderlight;
        @media (max-width: 991px) {
          padding: 20px 16px;
        }
        &-text {
          @extend .s1;
        }
        svg path {
          fill: $f-black;
        }
        &:nth-child(odd) {
          border-right: 1px solid $ui-borderlight;
        }
        &:hover {
          color: $f-primary;
          .link-text__desc {
            color: $f-primary;
          }
          svg path {
            fill: $f-primary;
          }
        }
      }
      &--two_column {
        grid-template-columns: repeat(2, 1fr);
        @media (max-width: 600px) {
          grid-template-columns: repeat(1, 1fr);
        }
        .link {
          padding: 56px 64px;
        }
      }
    }

    &-desc {
      padding: 40px 64px;
      @media (max-width: 991px) {
        padding: 24px 16px;
      }
      h4 {
        @extend .h4;
        margin-bottom: 40px;
      }
      p {
        @extend .p2;
        margin: 32px 0;
      }
    }
    .video-block {
      display: flex;
      flex-direction: column;
      margin-bottom: 24px;
      .youtube {
        position: relative;
        width: 100%;
        height: 434px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        @media (max-width: 991px) {
          height: 300px;
        }
        @media (max-width: 600px) {
          height: 250px;
        }
        @media (max-width: 375px) {
          height: 150px;
        }
        .play {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          background-image: url("data:image/svg+xml,%3Csvg width='140' height='140' viewBox='0 0 140 140' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M49.9935 36.1857L109.924 70L49.9935 103.814L49.9935 36.1857Z' stroke='white' stroke-width='4'/%3E%3Ccircle cx='70' cy='70' r='68' stroke='white' stroke-width='4'/%3E%3C/svg%3E%0A");
          background-size: cover;
          width: 140px;
          height: 140px;
          transition: 0.3s;
          cursor: pointer;
          @media (max-width: 991px) {
            width: 100px;
            height: 100px;
          }
          @media (max-width: 600px) {
            width: 75px;
            height: 75px;
          }
          @media (max-width: 375px) {
            width: 52px;
            height: 52px;
          }
          &:hover {
            background-image: url("data:image/svg+xml,%3Csvg width='140' height='140' viewBox='0 0 140 140' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M49.9935 36.1857L109.924 70L49.9935 103.814L49.9935 36.1857Z' fill='white' stroke='white' stroke-width='4'/%3E%3Ccircle cx='70' cy='70' r='68' stroke='white' stroke-width='4'/%3E%3C/svg%3E%0A");
          }
        }
      }
      span {
        @extend .s3;
        margin-top: 8px;
      }
    }
  }
  &__link-all {
    padding: 0 40px;
    background-color: $ui-light;
    border-top: 1px solid $ui-borderlight;
    margin-top: -1px;
    @media (max-width: 991px) {
      padding: 0 24px;
    }
    .link {
      &-text {
        @extend .s1;
      }
      svg {
        @media (min-width: 1367px) {
          width: 32px;
          height: 32px;
        }
        @media (max-width: px) {
        }
      }
    }
    svg path {
      fill: $ui-primary;
    }
  }
  &__doctor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    @media (max-width: 480px) {
      grid-template-columns: repeat(1, 1fr);
    }
    &-item {
      padding: 32px;
      border-bottom: 1px solid $ui-borderlight;
      &:nth-child(odd) {
        border-right: 1px solid $ui-borderlight;
      }
      @media (max-width: 991px) {
        padding: 16px;
      }
    }
    &-img {
      position: relative;
      height: 512px;
      overflow: hidden;
      margin-bottom: 24px;
      @media (max-width: 991px) {
        height: 350px;
      }
      @media (max-width: 600px) {
        height: 250px;
      }
      @media (max-width: 480px) {
        height: 400px !important;
      }
      @media (max-width: 320px) {
        height: 324px !important;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        object-fit: cover;
      }
    }
    &-title {
      @extend .h3;
      margin-bottom: 16px;
      padding: 0 28px 0 32px;
      @media (max-width: 991px) {
        color: $f-primary;
      }
    }
    &-desc {
      @extend .p3;
      padding: 0 28px 0 32px;
    }
  }
  &__reviews {
    .review {
      padding: 40px;
      border-bottom: 1px solid $ui-borderlight;
      @media (max-width: 991px) {
        padding: 24px 16px;
      }
    }
  }
  &__problem {
    padding: 40px;
    @media (max-width: 991px) {
      padding: 24px 16px;
    }
    h2 {
      @extend .h2;
      margin-bottom: 32px;
      @media (max-width: 991px) {
        margin-bottom: 16px;
      }
    }
    h3 {
      @extend .h3;
      color: $f-primary;
      margin-bottom: 26px;
      @media (max-width: 991px) {
        margin-bottom: 8px;
      }
    }
    p {
      @extend .p2;
      margin: 32px 0;
      @media (max-width: 991px) {
        margin: 8px 0;
      }
    }
    img {
      margin: 32px 0;
      @media (max-width: 991px) {
        margin-top: 8px;
        margin-bottom: 16px;
      }
    }
  }
  &__result {
    padding: 40px;
    @media (max-width: 991px) {
      padding: 24px 16px;
    }
    &-desc {
      @extend .p2;
    }
  }
  &__prices {
    padding: 40px 64px;
    @media (max-width: 991px) {
      padding: 20px 24px;
    }
    .attention {
      margin-bottom: 32px;
      @media (max-width: 991px) {
        margin-bottom: 8px;
      }
    }
    .price {
      @extend .p1;
      background-color: $ui-white;
    }
  }
  &__dropdown {
    padding: 64px 64px 40px;
    @media (max-width: 1366px) {
      padding: 40px;
    }
    @media (max-width: 991px) {
      padding: 0;
    }
    .dropdown {
      background-color: $ui-white;
      padding: 28px 0;
      border-bottom: 1px solid $ui-borderlight;
      @media (max-width: 991px) {
        padding: 0;
      }
      &:last-child {
        border: none;
      }

      &.active {
        background-color: $ui-light2;
        border-bottom: none;
        margin: -1px -64px 0;
        padding: 64px;
        @media (max-width: 1366px) {
          margin: -1px -40px 0;
          padding: 40px;
          &:first-child {
            margin: -40px -40px 0;
          }
        }
        @media (max-width: 991px) {
          margin: 0;
          padding: 0;
          &:first-child {
            margin: 0;
          }
        }
        &:first-child {
          margin: -64px -64px 0;
        }
        .dropdown__caption {
          color: $f-primary;
          svg {
            transform: rotate(180deg);
            path {
              stroke: $f-primary;
            }
          }
        }
      }
      &__caption {
        @extend .h2;
        color: $f-black;
        @media (max-width: 991px) {
          padding: 16px;
        }
      }
      &__content {
        margin-top: 50px;
        background: inherit;
        @media (max-width: 1366px) {
          margin-top: 30px;
        }
        @media (max-width: 991px) {
          margin-top: 0;
        }
        .price {
          @extend .h3;
          &__item {
            @media (max-width: 1366px) {
              margin-bottom: 0;
            }
            @media (max-width: 991px) {
              padding: 16px;
              &:first-child {
                border-top: 1px solid $ui-borderlight;
              }
              &:last-child {
                border-bottom: 1px solid $ui-borderlight;
              }
            }
            &:last-child {
              margin-bottom: 0;
            }
            &-prop {
              max-width: 630px;
            }
          }
        }
        .link-desc {
          @extend .s1;
          color: $f-black;
          border-bottom: 1px solid $ui-borderlight;
          width: 100%;
          padding: 32px 0;
          @media (max-width: 991px) {
            padding: 32px 16px;
          }
          &:last-child {
            // padding-bottom: 0;
            border: none;
          }
          &:hover {
            color: $f-primary;
            svg path {
              fill: $f-primary;
            }
          }
          .link-text {
            flex-grow: 1;
            &__title {
              display: flex;
              justify-content: space-between;
              align-items: center;
              span {
                flex-basis: 60%;
              }
              @media (max-width: 991px) {
              }
            }
            &__desc {
              @media (min-width: 992px) {
                display: none;
              }
            }
          }
        }
      }
    }
  }
  .detail__allprices-desc {
    background-color: $ui-light;
    margin: -1px -64px -40px;
    padding: 40px;
    @media (max-width: 1366px) {
      margin: -1px -40px -40px;
    }
    @media (max-width: 991px) {
      padding: 16px;
      margin: 0;
    }
    p {
      margin: 0;
      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }
  }
  &__certificates {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    &-item {
      padding: 40px 64px;
      border-bottom: 1px solid $ui-borderlight;
      &:nth-child(odd) {
        border-right: 1px solid $ui-borderlight;
      }
      @media (max-width: 1600px) {
        padding: 40px;
      }
      @media (max-width: 992px) {
        padding: 24px;
      }
      @media (max-width: 480px) {
        padding: 16px !important;
      }
    }
    &-img {
      position: relative;
      height: 250px;
      @media (max-width: 1366px) {
        height: 211px;
      }
      @media (max-width: 600px) {
        height: 160px;
      }
      @media (max-width: 375px) {
        height: 128px;
      }
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-title {
      margin-top: 8px;
      @extend .s3;
    }
  }
  .pagination {
    @extend .p2;
  }
}
