// В этом файле должны быть стили для БЭМ-блока form-enroll, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.form-enroll {
  $block-name: &; // #{$block-name}__element
  margin-top: 114px;
  padding-top: 24px;
  flex-grow: 1;
  z-index: 999;
  // max-height: 559px;
  // @media (max-width: 1600px) {
  //   max-height: 419px;
  // }
  @media (max-width: 991px) {
    margin-top: 0;
    padding: 24px 0 44px 0;
    background-color: rgba(23, 55, 85, 0.7);
  }
  &__text {
    grid-column: 3 span;
    @media (max-width: 1366px) {
      grid-column: 12 span;
      grid-row: 1;
      display: flex;
      justify-content: space-between;
    }
    @media (max-width: 991px) {
      grid-column: 8 span;
      flex-direction: column;
    }
  }
  &__title {
    @extend .h2;

    color: $f-white !important;
    @media (max-width: 1366px) {
      max-width: 465px;
    }
    @media (max-width: 991px) {
      @include font(20px, 26px, 400);
      margin-bottom: 24px;
    }
  }
  &__desc {
    @extend .s2;

    color: $f-white !important;
    @media (max-width: 1366px) {
      max-width: 420px;
    }
    @media (max-width: 991px) {
      @include font(16px, 20px, 400);
    }
  }
  form {
    grid-column: 4 / 9 span;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 1fr 88px;
    gap: 5px;
    @media (max-width: 1366px) {
      grid-column: 12 span;
      grid-row: 2;
    }
    @media (max-width: 991px) {
      grid-column: 8 span;
      margin-top: 24px;
    }
    input,
    textarea {
      background-color: $ui-input;
      border: none;
      padding: 40px;
      @extend .s3;
      color: $f-white;
      &::placeholder {
        color: $f-white;
      }
      @media (max-width: 991px) {
        padding: 16px 20px;
      }
    }
    input {
    }
  }
  &__name {
    @media (max-width: 991px) {
      grid-column: 8 span;
      grid-row: 1;
    }
  }
  &__tel {
    @media (max-width: 991px) {
      grid-column: 8 span;
      grid-row: 2;
    }
  }
  &__textarea {
    grid-column: 2;
    grid-row: 1 / 2 span;
    // align-self: center;
    overflow: hidden;
    resize: none;
    &::placeholder {
      height: 100%;
      transform: translateY(calc(50% - 15px));
    }
    @media (max-width: 991px) {
      grid-column: 8 span;
      grid-row: 3;
    }
  }
  &__checkbox {
    @media (max-width: 991px) {
      grid-column: 8 span;
      grid-row: 4;
    }
  }
  &__send {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $ui-primary;
    padding: 32px 40px;
    cursor: pointer;
    span {
      @extend .s3;
      color: $f-white !important;
    }
    @media (max-width: 991px) {
      grid-column: 8 span;
      grid-row: 5;
      padding: 16px;
    }
  }
}
